






import Vue from 'vue'
import ResizerCollection from './resizer';

export default Vue.extend({
    name: 'Resizer',
    data:function(){
        return {
            ResizerTypes:['top', 'bottom', 'left', 'right', 'topleft', 'topright', 'bottomleft', 'bottomright'],
            ifInit:false
        }
    },
    props:{
        target:{
            type: Element
        },
        minWidth:{
                type:Number,
                default:600
            },
        minHeight:{
                type:Number,
                default:400
            }
    },
    watch:{
        target:function(el:Element){
            if(this.$data.ifInit == false) {
                new ResizerCollection(this, this.$props.target, this.minWidth, this.minHeight);
                this.$data.ifInit = true;
            }
        }
    }
})
