







import Vue from 'vue'
import PopupInfo, { popupDirection } from './popup-info';
export default Vue.extend({
    name:"Popup",
    props:{
        popupInfo:{
            type:PopupInfo
        },
    },
    computed:{
        directionStyle:function(){
            let direction = this.popupInfo.direction as object
            return direction;
        }
    }
})
