











import Vue, { PropType } from 'vue'
import UiButton from '../../ui-components/button.vue'
import Window from './window.vue'

export default Vue.extend({
    name:'Window-title',
    props:{
        iconPath:String,
        title:String,
        hasMinimizer:Boolean,
        hasMaximizer:Boolean,
        targetWindow:{
            type:Window,
            required:true
        }
    },
    components:{
        UiButton
    },
    methods:{
        close(e:Event){
            (this.targetWindow as any).close();
        }
    }
})
